<template>
	<div>
		<router-link 
			tag="div" 
			to="/" 
			class="header-abs"
			v-show="showAbs"
		>
			<div class="iconfont header-abs-back">&#xe624;</div>
		</router-link>
		<div 
			class="header-fixed-back" 
			v-show="!showAbs"
			:style="opacityStyle"
		>
			<router-link to="/">
				<div class="iconfont header-back">&#xe624;</div>
			</router-link>
			景点详情
		</div>
	</div>
</template>
<script type="text/javascript">
export default {
	name:"detailHeader",
	data(){
		return {
			showAbs:true,
			opacityStyle:{
				opacity:0
			}
		}
	},
	mounted(){

	},
	methods:{
		handleScroll(){
			const top=document.documentElement.scrollTop;
			if(top>60 ){
				let opacity=top/140;
				opacity =opacity > 1 ? 1 : opacity;
				this.opacityStyle= { opacity };
				this.showAbs=false
			}else{
				this.showAbs=true
			}
			console.log(top);
		}
	},
	activated(){
		window.addEventListener("scroll",this.handleScroll)
	},
	deactivated(){
		window.removeEventListener("scroll",this.handleScroll)
	}
}
</script>

<style type="text/css" scoped lang="stylus">
@import '~styles/varibles.styl'
.header-abs{
	position:absolute;
	left:.2rem;
	top:.2rem;

	width: .8rem;
	height: .8rem;
	line-height: .8rem;
	border-radius: .4rem;
	background: rgba(0,0,0,.8);
}	
.header-abs-back{
	font-size: .4rem;
	color: #fff;
	text-align: center;
}
.header-fixed-back{
	position: fixed;
	top:0;
	left:0;
	right:0;
	z-index: 3;
	height:$headerHeight;
	line-height: $headerHeight;
	text-align: center;
	color: #fff;
	background: $bgColor 
}
.header-back{
	position: absolute;
	top:0;
	left:0;
	width: .64rem;
	font-size: .4rem;
	text-align: center;
	color: #fff;
}
</style>



